import React, { useEffect } from 'react';
import { Form, Button, Space, Input } from 'antd';

import customConfig from '../../custom_config';

// Form 组件

const formCommonStyle = customConfig.formCommonStyle;
const styleFormLayout = formCommonStyle.layoutForm;
const styleFormItemLayout = formCommonStyle.formItemLayout;
const styleFormTailItemLayout = formCommonStyle.tailFormItemLayout;

const CustomForm = ({
  columns,
  disabledKeys,
  notShowKeys,
  formKeyLabelObj,
  formKeySelectComponentsObj,
  onFinish,
  cancelHandle,
  deleteHandle,
  showDeleteButton,
  updateFormObj,
  formInitialObj,
  initFormCount
}) => {
  const [form] = Form.useForm();

  useEffect(() => {
    // formInitialObj 变化 重新给form赋值
    console.log(updateFormObj);
    form.setFieldsValue(updateFormObj);
  }, [updateFormObj]);

  useEffect(() => {
    // initFormCount 变化 重新给form赋初始化值
    form.setFieldsValue(formInitialObj);
  }, [initFormCount]);

  return (
    <Form {...styleFormLayout} form={form}
      layout="horizontal" onFinish={onFinish} name="adminer_info_edit">
      {columns.map((key) => {
        if (notShowKeys.includes(key)) {
          return null;
        }
        const disabled = disabledKeys.includes(key);  // 是否可以编辑输入框
        const currentLabel = formKeyLabelObj[key];
        let currentInputValue = updateFormObj[key];
        return (
          <Form.Item
            {...styleFormItemLayout}
            name={key}
            label={currentLabel}
            key={key.toString()}
            initialValue={currentInputValue}
          >
            {Object.keys(formKeySelectComponentsObj).includes(key)
              ? formKeySelectComponentsObj[key]
              : <Input placeholder={`请输入 ${currentLabel}`} disabled={disabled} />}
          </Form.Item>
        )
      })}
      <Form.Item {...styleFormTailItemLayout}>
        <Space>
          <Button type="primary" htmlType="submit">提交</Button>
          <Button onClick={cancelHandle}>取消</Button>
          {showDeleteButton ? <Button onClick={deleteHandle}>删除</Button> : null}
        </Space>
      </Form.Item>
    </Form>
  )
};

CustomForm.defaultProps = {
  columns: [],
  disabledKeys: [],
  notShowKeys: [],
  formKeyLabelObj: {},
  formKeySelectComponentsObj: {},
  onFinish: (values) => { console.log(values); },
  cancelHandle: () => { console.log('Cancel Handle'); },
  deleteHandle: () => { console.log('Delete Handle'); },
  showDeleteButton: false,
  updateFormObj: {},
  formInitialObj: {},
  initFormCount: 0
};

export { CustomForm };
